.post {
    display: flex;
    align-items: center;
    gap: 20px;
    margin-bottom: 15px;
    max-width: 900px;
    line-height: 18px;
    letter-spacing: 1px;
    font-size: 15px;
    border-bottom: 1px rgb(211, 206, 206) solid;
    padding-bottom: 10px;
}

.post__avatar img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}